<template>
  <div>
    <span class="se-pre-con hide">
        <div class="center_text text">{{statusMessage}}.</div>
    </span>
    <section>
      <article class="form">
        <ul>
          <li>
            <div>
              <label for="player_ids" class="text">Steam32 Ids:</label>
              <textarea id="player_ids" v-model="inputPlayers" value="" placeholder="Please input a list of steam urls, separate by ," class="player_ids" />
              <div>
                <span class="text">{{limit_message}}</span>
              </div>
            </div>
          </li>
          <li>
            <div>
              <label for="period" class="text">Select Period:</label>
              <select id="period" v-model="period">
                <option value="7">Last Week</option>
                <option value="30">Last Month</option>
                <option value="365">Last Year</option>
              </select>
            </div>
          </li>
          <li>
            <div>
              <label for="use_pro_data" class="text">Use Pro Player Data</label>
              <input id="use_pro_data" type="checkbox" v-model="use_pro_data">
            </div>
          </li>
          <li>
            <div>
              <button @click="view('leaderboard')">View Leaderboard</button>
              <button @click="view('compare')">Compare</button>
            </div>
          </li>
        </ul>
      </article>
    </section>
  </div>
</template>

<script>
import axios from "axios"
import config from "../config"
import * as func from "../helper/functions"
import SteamID from "../helper/steamid"

export default {
  name: "Home",
  data() {
    return {
      env: process.env.NODE_ENV,
      config: config,
      inputPlayers:
        process.env.NODE_ENV == "development"
          ? "177941583,76561197960287930,gabelogannewell,STEAM_0:0:11101,[U:1:22202],U:1:22202,http://steamcommunity.com/id/102844872920382392323455/,http://steamcommunity.com/id/March8/,http://steamcommunity.com/profiles/76561197972496930/,http://steamcommunity.com/id/misterdurst69/,,http://steamcommunity.com/id2/misterdurst69/"
          : "",
      period: 7,
      use_pro_data: false,
      players: {},
      limit_message: `Only the first ${config.GET_LIMIT} players are used.`,
      statusMessage: ""
    }
  },
  mounted() {
    const requestInterceptor = axios.interceptors.request.use(
      config => {
        if (config.data && config.data.vanityUrl) {
          this.statusMessage = `Commencing Order 66`
        }
        return config
      },
      error => {
        this.statusMessage = `Failed sending request to ${config.url}`
        return Promise.reject(error)
      }
    )
  },
  methods: {
    async view(action) {
      // player id validation
      let playerIds = []
      if (this.use_pro_data) {
        const proPlayerData = await axios.get("https://api.opendota.com/api/proPlayers")
        playerIds = proPlayerData.data.map(value => value.account_id).slice(0, config.GET_LIMIT)
      } else {
        if (this.inputPlayers.split(",").length < 2) {
          // TODO display errors better than alerts
          return alert("Please input at least two valid player ids. If you don't know how to get Steam ids, check on 'Use Pro Player Data' for sample data")
        }
        func.toggleLoading()
        // get vanity id
        playerIds = await Promise.all(this.parseSteamId())
        playerIds = playerIds.filter((item, i, ar) => ar.indexOf(item) === i) // remove duplicate
      }
      if (playerIds.length >= config.API_RPS_LIMIT) {
        return alert("We're likely to exceed the request per second limit (3). I'd rather stop you now.")
      }
      switch (action) {
        case "leaderboard":
          this.$router.push({
            name: "Leaderboard",
            params: { playerIds: playerIds, period: this.period, useProData: this.use_pro_data }
          })
          break
        case "compare":
          this.$router.push({
            name: "Compare",
            params: { playerIds: playerIds, period: this.period, useProData: this.use_pro_data }
          })
          break
      }
      // debugger
    },
    parseSteamId() {
      return this.inputPlayers
        .split(",")
        .map(value => {
          // vanityURL check
          const matches = value.match(/^(?:http[s]?:\/\/)?(?:www.)?steamcommunity.com\/id\/(.+)\/*$/)
          if (matches && matches[1]) {
            return this.convertVanityURL(matches[1].replace("/", ""))
          }
          // steam32 check
          if (value.match(/^(-?[0-9]+)$/)) {
            var raw = parseInt(value)
            // 32-bit range check
            if (raw < 2147483647 && raw > -2147483648) {
              return raw
            }
          }
          // steam64 check
          if (value.match(/^7656[0-9]+$/)) {
            // convert to raw (subtract base)
            var raw = parseInt(value.substring(4)) - 1197960265728

            // sanity range check.
            if (raw > 0 && raw < 68719476736) {
              return SteamID.Parse(value, SteamID.Format.STEAMID64).value
            }
          }
          // vanity url
          if (value.match(/^[a-zA-Z0-9_-]{2,}$/)) {
            return this.convertVanityURL(value)
          }
          return SteamID.Parse(value) ? SteamID.Parse(value).value : null
        })
        .filter(n => n != undefined)
        .slice(0, config.GET_LIMIT)
    },
    async convertVanityURL(vanityUrl) {
      const response = await axios.post(this.config.STEAM_PROXY_API, {
        vanityUrl,
        headers: {
          "Content-Type": "application/json"
        },
        data: {
          url: `${this.config.STEAM_API}/ResolveVanityURL/v0001/?vanityurl=${vanityUrl}`
        }
      })
      const steam = SteamID.Parse(response.data.response.steamid, SteamID.Format.STEAMID64)
      if (steam) {
        return steam.value
      }
    }
  }
}
</script>

<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0;
}
a {
  color: #42b983;
}
.player_ids {
  margin: 0;
  width: 400px;
  height: 100px;
}
article.form,
article.form li {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  width: 800px;
  margin: 0 auto;
}
</style>



